import { nearToYoktoNear, useNear } from "lib/near";
import { usePayment } from "models/payment";
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";

const basePath = `${window.location.origin}/payment`;

export const useModel = () => {
  const { payment, paymentId } = usePayment();
  const near = useNear();
  const navigate = useNavigate();

  useEffect(() => {
    if (!near.isReady) {
      return;
    }

    if (!near.isAuthorized) {
      navigate(`/payment/${paymentId}`);

      return;
    }

    if (payment) {
      const yoktoNearAmount = nearToYoktoNear(+payment.displayAmount);

      if (!yoktoNearAmount || !payment.address) {
        throw new Error("Data is empty");
      }

      near.send(
        yoktoNearAmount,
        payment.address,
        `${basePath}/${payment.paymentId}/pending`
      );
    }
  }, [payment, near, paymentId, navigate]);
};
